// @flow
import _get from 'lodash.get';
import type { Offer as OfferType } from 'types/Offer';
import {
  OFFER_STEPS,
  FORM_TYPE_IDS,
  FUNDING_PURCHASE_TYPES,
} from 'shared/Consts';
import { formatPrice } from 'helpers/number';
import _isEmpty from 'lodash/isEmpty';

const VALID_STEPS = {
  ...OFFER_STEPS,
  viewOriginalOffer: 101,
  contingencyRemoval: 801,
};

export const STEPS = [
  {
    id: VALID_STEPS.pleaseComplete,
    name: 'Continue Offer',
  },
  {
    id: VALID_STEPS.viewOriginalOffer,
    name: 'View Original Offer',
  },
  {
    id: VALID_STEPS.counteredBySeller,
    name: 'Negotiate',
  },
  {
    id: VALID_STEPS.confirmTransfer,
    name: 'Wire',
  },
  {
    id: VALID_STEPS.scheduleAProperty,
    name: 'Inspection',
  },
  {
    id: VALID_STEPS.submitCorrectiveAction,
    name: 'SCAR',
  },
  {
    id: VALID_STEPS.contingencyRemoval,
    name: 'Contingency Removal',
  },
];

type isEnabledStepParams = {
  offer: OfferType,
  stepId: number,
};

export const isEnabledStep = ({
  offer,
  stepId,
}: isEnabledStepParams): boolean => {
  const offerStep = _get(offer, 'StepId', 0);
  const isPleaseCompleteEnabled =
    stepId === VALID_STEPS.pleaseComplete &&
    offerStep === VALID_STEPS.pleaseResubmit; // Combine step 1 with step 150

  const isViewOriginalOfferEnabled =
    stepId === VALID_STEPS.viewOriginalOffer &&
    offerStep > VALID_STEPS.pleaseComplete;

  const contingencies = _get(offer, 'Contingencies');
  const isContingencyRemovalEnabled =
    stepId === VALID_STEPS.contingencyRemoval &&
    !_isEmpty(contingencies) &&
    !_get(contingencies, 'AllRemoved');

  return (
    isPleaseCompleteEnabled ||
    isViewOriginalOfferEnabled ||
    isContingencyRemovalEnabled ||
    offerStep === stepId
  );
};

const getFormLink = ({ offerId, stepId, formId }) =>
  `/offer?offerId=${offerId}&step=${OFFER_STEPS.pleaseComplete}&formId=${formId}`;

const getOfferSubmitLink = (offer: OfferType): Object => {
  return {
    redirectTo: getFormLink({
      offerId: _get(offer, ['OfferId']),
      stepId: OFFER_STEPS.pleaseComplete,
      formId: _get(offer, ['Forms', 0, 'FormId']),
    }),
  };
};

const getSubmitCorrectiveActionLink = (offer: OfferType): Object => {
  const forms = _get(offer, ['Forms']) || [];
  const scarForm = forms.find(
    form => _get(form, ['FormTypeId']) === FORM_TYPE_IDS.scar,
  );
  const formId = _get(scarForm, ['FormId']);

  return {
    redirectTo: getFormLink({
      offerId: _get(offer, ['OfferId']),
      stepId: OFFER_STEPS.submitCorrectiveAction,
      formId,
    }),
  };
};

const getContingencyRemovalLink = (offer: OfferType): Object => {
  const forms = _get(offer, ['Forms']) || [];
  const removalForm = forms.find(
    form => _get(form, ['FormTypeId']) === FORM_TYPE_IDS.contingencyRemoval,
  );
  const formId = _get(removalForm, ['FormId']);

  return {
    redirectTo: getFormLink({
      offerId: _get(offer, ['OfferId']),
      stepId: OFFER_STEPS.confirmTransfer,
      formId,
    }),
  };
};

type getStepActionParams = isEnabledStepParams & {
  openOfferDialogHandler: Function,
  openConfirmTransferHandler: Function,
  openScheduleInspectionHandler: Function,
  openOriginalOfferDialogHandler: Function,
};

export const getStepAction = ({
  offer,
  stepId,
  openOfferDialogHandler,
  openConfirmTransferHandler,
  openScheduleInspectionHandler,
  openOriginalOfferDialogHandler,
}: getStepActionParams): Object => {
  switch (stepId) {
    case VALID_STEPS.pleaseComplete: {
      return getOfferSubmitLink(offer);
    }
    case VALID_STEPS.viewOriginalOffer: {
      return {
        onClickHandler: openOriginalOfferDialogHandler,
      };
    }
    case VALID_STEPS.counteredBySeller: {
      return {
        onClickHandler: openOfferDialogHandler,
      };
    }
    case VALID_STEPS.confirmTransfer: {
      return {
        onClickHandler: openConfirmTransferHandler,
      };
    }
    case VALID_STEPS.scheduleAProperty: {
      return {
        onClickHandler: openScheduleInspectionHandler,
      };
    }
    case VALID_STEPS.submitCorrectiveAction: {
      return getSubmitCorrectiveActionLink(offer);
    }
    case VALID_STEPS.contingencyRemoval: {
      return getContingencyRemovalLink(offer);
    }

    default:
      return { redirectTo: '/' };
  }
};

type fieldType = {
  label: string,
  value: string | number,
};

export const getOriginalOfferDialogFields = (
  offer: OfferType,
): Array<fieldType> => {
  if (!offer || _isEmpty(offer)) return [];

  const funding = _get(offer, 'Funding.PurchaseType', '');

  return [
    { label: 'Offer No', value: _get(offer, 'OfferNo', '') },
    {
      label: 'offer price',
      value: _get(offer, 'MappingCounter.OfferPrice')
        ? formatPrice(_get(offer, 'MappingCounter.OfferPrice'))
        : 'TBD',
    },
    {
      label: 'funding',
      value: funding,
    },
    {
      label: 'Loan Type',
      value:
        funding === FUNDING_PURCHASE_TYPES.cash
          ? 'N/A'
          : _get(offer, 'Funding.LoanType', ''),
    },
    {
      label: 'finance contingency',
      value:
        funding === FUNDING_PURCHASE_TYPES.cash
          ? 'N/A'
          : _get(offer, 'Contingencies.FinanceContingency', ''),
    },
    {
      label: 'due diligence contingency',
      value: _get(offer, 'MappingCounter.InspectionContingency', ''),
    },
    {
      label: 'home sale contingency',
      value: _get(offer, 'MappingCounter.SaleOfHomeContingency', ''),
    },
    {
      label: 'Closing Period',
      value: _get(offer, 'MappingCounter.ClosingPeriod', ''),
    },
    {
      label: 'Furniture',
      value: _get(offer, 'MappingCounter.Furniture', ''),
    },
  ];
};
