// @flow
import React, { Fragment } from 'react';
import _get from 'lodash.get';
import { makeStyles } from '@material-ui/core/styles';
import type { Element } from 'react';
import { Widget } from 'components/Widget';
import type { Offer as OfferType } from 'types/Offer';
import _isEmpty from 'lodash/isEmpty';
import Step from './Step';
import { STEPS, isEnabledStep, getStepAction } from './helpers';

const useStyles = makeStyles(theme => ({
  widget: {
    marginBottom: theme.spacing(3),
  },
  description: {
    ...theme.typography.h5,
    marginBottom: theme.spacing(3),
    color: theme.palette.primary.gray,
  },
}));

type Props = {
  offer: OfferType,
  openOfferDialogHandler: Function,
  openConfirmTransferHandler: Function,
  openScheduleInspectionHandler: Function,
  openOriginalOfferDialogHandler: Function,
};

const Offer = ({
  offer = {},
  openOfferDialogHandler,
  openConfirmTransferHandler,
  openScheduleInspectionHandler,
  openOriginalOfferDialogHandler,
}: Props): Element<'div' | typeof Fragment> => {
  const classes = useStyles();

  if (_isEmpty(offer)) return <></>;

  const offerStep = _get(offer, 'StepId', 0);

  return (
    <Widget className={classes.widget} title="Home Purchase Action Steps">
      <div className={classes.description}>
        To purchase property{' '}
        <strong>{_get(offer, 'Property.PropertyData.FullAddress', '')}</strong>{' '}
        you need to complete the steps below:
      </div>
      {STEPS.map(step => {
        const stepId = _get(step, 'id');
        const isEnabled = isEnabledStep({ offer, stepId });
        const isFinished = stepId < offerStep;
        const action = isEnabled
          ? getStepAction({
              offer,
              stepId,
              openOfferDialogHandler,
              openConfirmTransferHandler,
              openScheduleInspectionHandler,
              openOriginalOfferDialogHandler,
            })
          : {};
        return (
          <Step
            step={step}
            key={stepId}
            isEnabled={isEnabled}
            isFinished={isFinished}
            offer={offer}
            action={action}
          />
        );
      })}
    </Widget>
  );
};

export default Offer;
